<template>
    <header :class="{ 'scrolled-nav': scrolledNav }">
        <nav>
            <div class="breading">
                <img src="../assets/img/logo.png">

            </div>

            <ul class="navigation" v-show="!mobile">
                <li>
                    <router-link to="/" class="link">Home</router-link>
                </li>
                <li>
                    <router-link to="/about" class="link">About</router-link>
                </li>
                <li>
                    <router-link to="/project" class="link">Portfolio</router-link>
                </li>
                <li>
                    <router-link to="/contact" class="link">Contact</router-link>
                </li>
            </ul>
            <div class="icon">
                <i @click="toggleMobileNav" v-show="mobile" class="fa-solid fa-bars"
                    :class="{ 'icon-active': mobileNav }"></i>
            </div>
            <Transition name="mobile-nav">
                <ul class="dropdown-nav" v-show="mobileNav">
                    <li>
                        <router-link to="/" class="link">
                            <i class='bx bx-home me-5 text-[16px]'></i>Home
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/about" class="link">
                            <i class='bx bx-user me-5 text-[16px]'></i>About
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/project" class="link">
                            <i class='bx bx-briefcase-alt-2 me-5 text-[16px]'></i>Portfolio
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/contact" class="link">
                            <i class='bx bx-message-square-detail me-5 text-[16px]'></i>Contact
                        </router-link>
                    </li>
                </ul>
            </Transition>
        </nav>
        <slot></slot>
    </header>
</template>

<script>

export default {
    name: "HeaderMenu",

    data() {
        return {
            scrolledNav: null,
            mobile: null,
            mobileNav: null,
            windowWidth: null,
        }
    },
    methods: {
        toggleMobileNav() {
            this.mobileNav = !this.mobileNav
            this.toggleClass = 'fa-xmark'
        },
        checkScreen() {
            this.windowWidth = window.innerWidth;
            if (this.windowWidth <= 750) {
                this.mobile = true;
                return;
            }
            this.mobile = false
            this.mobileNav = false;
            return;
        },
        updateScroll() {
            const scrollPosition = window.scrollY;
            if (scrollPosition > 50) {
                this.scrolledNav = true;
                return;
            }
            this.scrolledNav = false;
            return;

        }
    },
    created() {
        window.addEventListener('resize', this.checkScreen);
        this.checkScreen()
    },
    mounted() {
        window.addEventListener('scroll', this.updateScroll);
    },
}
</script>

<style scoped>
.router-link-exact-active {
    color: #00afea !important;
}

.mobile-nav .router-link-exact-active {
    background: #00afea !important;
    color: #000 !important;

}

header {
    background: linear-gradient(135deg, #09004d 0%, #000 100%);
    z-index: 100;
    width: 100%;
    position: fixed;
    transition: .5s ease all;
    color: #fff;
    top: 0;
}



nav {
    display: flex;
    flex-direction: row;
    padding: 12px 0;
    transition: .5s ease all;
    width: 90%;
    margin: 0 auto;
    position: relative;
}

ul,
.link {
    font-weight: 500;
    color: #fff;
    list-style: none;
    text-decoration: none;
}

li {
    text-transform: uppercase;
    padding: 16px;
    margin-left: 16px;
}

.link {
    font-size: 14px;
    transition: .5s ease all;
    padding-bottom: 4px;
    /* border-bottom: 1px solid transparent; */
}

.link:hover {
    color: #00afea;
}

.breading {
    display: flex;
    align-items: center;
}

.breading img {
    width: 50px;
    transition: .5s ease all;
}

.navigation {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1px;
}

.icon {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: 24px;
    height: 100%;
}

.icon i {
    cursor: pointer;
    font-size: 24px;
    transition: .8s ease all;
}

.icon .icon-active {
    transform: rotate(180deg);

}

.dropdown-nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    max-width: 250px;
    height: 100%;
    background: #fff;
    color: #fff !important;
    top: 0;
    left: 0;

}

.dropdown-nav li {
    margin-left: 0;
    cursor: pointer;
    margin-bottom: 10px;
    transition: .5s ease all;
}

.dropdown-nav li:hover {

    background-color: #00afea;
    color: #fff !important;
}

.dropdown-nav li:hover .link {
    /* border-color: #00afea; */
    color: #000 !important;
}

.dropdown-nav li .link {
    color: #000;
}

/* scrolled nav */
.scrolled-nav {
    background-color: #000;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.scrolled-nav nav {
    padding: 8px 0;
}

.scrolled-nav nav .breading img {
    width: 40px;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

/* mobile nav */
.mobile-nav-enter-active,
.mobile-nav-leave-active {
    transition: 1s ease all;
}

.mobile-nav-enter-from,
.mobile-nav-leave-to {
    transform: translateX(-250px);
}

.mobile-nav-enter-to {
    transform: translateX(0);
}

@media (min-width:1440px) {
    nav {
        max-width: 1440px
    }
}
</style>
